@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

body{color:#333;font-family:'PT Sans', 'Arial', 'sans-serif'}

.wrapper{margin:auto;max-width:1200px}

.header__top{background:linear-gradient(90deg, #007941 0%, #00ab7b 100%);height:35px}.header__bottom{align-items:center;border-bottom:1px solid #ebebeb;display:flex;padding:25px 35px}.header__logo{margin-right:40px}.header__text{border-left:1px solid #333;font-size:18px;padding-left:15px}

.footer{bottom:0;box-sizing:border-box;position:absolute;width:100%}.footer__delim{background:linear-gradient(90deg, #007941 0%, #00ab7b 100%);height:4px}.footer__main{display:flex;justify-content:space-between;padding:25px 15px}.footer__logo{height:30px}.footer__copyright{font-size:14px}

